<template>
  <div id="map" class='rel'>
    <FeatureMap />
  </div>
</template>

<script>
import store from '../../store/index'
import axios from 'axios'
import * as d3 from 'd3'
import FeatureMap from '../Features/FeatureMap.vue'

var w = window.outerWidth
var h = 50
var transitionVar = 200
var greyScale = [61, 28]
export default {
  name: 'CoverageMap',
  components: {
    FeatureMap
  },
  data () {
    return {
      seqLength: 0,
      scale: null,
      coverageMapsrw: 0,
      residueColor: null,
      densityScale: null,
      darkRegions: [],
      myArray: [1]
    }
  },
  computed: {
    feature () {
      return store.state.feature
    },
    secondaryStructure () {
      return store.state.secondaryStructure
    },
    sequence () {
      return store.state.structure
    }
  },
  watch: {
    feature (newCount, oldCount) {
      var _this = this
      if (newCount.set === null) {
        _this.selectStructmap(window.AQUARIA.currentMember)
      } else {
        _this.setStructFeatmap(window.AQUARIA.currentMember)
        window.addEventListener('resize', function () {
          _this.setStructFeatmap(window.AQUARIA.currentMember)
        })
      }
    },
    secondaryStructure () { // Update coverage map when new structure selected
      var obj = {}
      obj.set = null
      store.commit('setFeature', obj)
      this.selectStructmap(window.AQUARIA.currentMember)
    },
    sequence (newVal, oldVal) {
      var _this = this
      if (oldVal.accession !== newVal.accession && oldVal.accession !== null) { // Only redraw coverage map if new chain
        document.querySelector('#map > #coverageMap').remove()
        _this.init(newVal.accession)
        var loadRequest = {
          selector: [newVal.accession],
          selectPDB: newVal.pdb, // optional fields
          selectChain: newVal.chain // optional fields
        }
        var url = `${window.BACKEND}/get_3D_alignment`
        axios({
          method: 'get',
          url: url,
          params: loadRequest
        }).then(function (response) {
          const newData = response.data
          store.commit('setSecondaryStructure', newData.secondary_structure)
        })
      }
    }
  },
  mounted () {
    this.init(this.$route.params.uniprotAccession)
  },
  methods: {
    init (accession) {
      var _this = this
      var url = `${window.BACKEND}/getCoverageMap`
      var loadRequest = {
        selector: [accession]
      }
      axios({
        method: 'get',
        url: url,
        params: loadRequest
      }).then(function (response) {
        console.log(response.data)
        _this.drawMap(response.data)
        window.addEventListener('resize', function () {
          document.querySelector('#map > #coverageMap').remove()
          _this.drawMap(response.data)
          _this.selectStructmap(window.AQUARIA.currentMember)
        })
      })
    },
    drawMap (data) {
      var _this = this
      var svg = _this.renderSVG()
      svg.append('rect')
        .attr('id', 'selection')
      this.drawLine(svg)
      this.setScales(data)
      data.secondaryStructures.forEach(function (d) {
        // convert strings to numbers
        d.start = +d.start
        d.end = +d.end
        _this.drawResidues(d, svg, data)
      })
      this.drawDarkRegions(data, svg)
    },
    setScales (data) {
      this.seqLength = data.seqLength
      this.scale = d3.scale.linear().domain([1, this.seqLength]).range([1, window.innerWidth]) // Scale seq length by screen width
      this.coverageMapsrw = (this.scale(data.seqLength)) / (data.seqLength) // Single residue width
      var maxStruct = Math.max(...data.densityMap)
      if (maxStruct <= transitionVar) { // Use absolute scale for density map when total number of struct < 200
        this.densityScale = d3.scale.linear().domain([Math.log(1), Math.log(transitionVar)]).range(greyScale)
      } else { // Use relative scale for density map when total number of struct > 200
        this.densityScale = d3.scale.linear().domain([Math.log(1), Math.log(Math.max(...data.densityMap))]).range(greyScale)
      }
    },
    drawLine (svg) {
      svg
        .append('line')
        .style('stroke', '#434343')
        .attr('x1', 1)
        .attr('y1', 26)
        .attr('x2', window.innerWidth)
        .attr('y2', 26)
    },
    renderSVG () {
      var svg = d3
        .select('#map')
        .append('svg')
        .attr('id', 'coverageMap')
        .attr('width', w)
        .attr('height', h)

      var rect = svg.append('g')
      return rect
    },
    drawResidues (el, svg, data) {
      // var thickness = 0
      var _this = this
      if (el.Type === 'SHEET') {
        var height = 18
        // var color = '#f6c719'
        var translate = 17
      } else if (el.Type === 'COIL') {
        height = 14
        // color = '#6f9e52'
        translate = 19
      } else {
        height = 19
        // color = '#6491b6'
        translate = 16
      }
      for (var n = el.start; n <= el.end; n++) {
        var color = 'hsl(0, 0%,' + _this.densityScale(Math.log(data.densityMap[n - 1])) + '%)'
        svg
          .append('g')
          .attr('id', 'residue_' + n + '_' + el.Type + '_' + data.densityMap[n - 1])
          .append('rect')
          .attr('transform', 'translate(' + (n * _this.coverageMapsrw - _this.coverageMapsrw) + ',' + translate + ')')
          .attr('width', function () {
            if (_this.coverageMapsrw < 0.5) {
              return _this.coverageMapsrw + 0.25
            } else {
              return _this.coverageMapsrw
            }
          })
          .attr('height', height)
          .attr('fill', color)
          .attr('stroke-width', 1)
          .on('mouseover', function (d) {
            _this.residueColor = d3.select(this).style('fill') // Store original residue color
            d3.select(this)
              .attr('stroke', 'white')
              .attr('stroke-width', 1)
            store.commit('setRes', this.parentElement.id.split('_')[1])
            store.commit('setPopupTitle', 'Residue ' + this.parentElement.id.split('_')[1])
            store.commit('setPopupText', 'Structures with residue ' + this.parentElement.id.split('_')[1] + ': ')
            store.commit('setTotalPDBres', this.parentElement.id.split('_')[3])
            store.commit('setPopupCoordinates', this.getBoundingClientRect())
          })
          .on('mouseout', function () {
            // store.commit('setPopupCoordinates', 0)
            d3.select(this)
              .attr('stroke', _this.residueColor)
              .attr('stroke-width', 0)
          })
      }
    },
    selectStructmap (selectedClus) {
      var _this = this
      // Select correct region in the coverage map
      d3.selectAll('[id^="residue_"')[0].map(function (d, i) {
        var density = d.id.split('_')[3]
        d3.select(d)
          .select('rect')
          .attr('fill', 'hsl(0, 0%,' + _this.densityScale(Math.log(density)) + '%)')
      })
      var secondaryStruct = store.getters.getSecondaryStructure[0]
      var seqStart = selectedClus.seq_start[0]
      var seqEnd = selectedClus.seq_end[selectedClus.seq_start.length - 1]
      var width = (seqEnd - seqStart) * _this.coverageMapsrw + _this.coverageMapsrw

      if (document.querySelector('#map > #coverageMap > rect')) {
        document.querySelector('#map > #coverageMap > rect').remove()
      }
      d3.select('#map > #coverageMap > g > #selection')
        .attr('width', width)
        .attr('height', 30)
        .attr('transform', 'translate(' + (seqStart * _this.coverageMapsrw - _this.coverageMapsrw) + ',' + 11 + ')')
        .attr('fill', 'white')
        .attr('stroke', '#434343')
        .attr('stroke-width', 1)
        .attr('rx', 6)
        .attr('ry', 6)

      secondaryStruct.map(function (index) {
        if (index.end) {
          var start = index.start
          var end = index.end
          var type = index.type
        } else {
          start = index.split('\n')[0].split(':')[1].split('-')[0]
          end = index.split('\n')[0].split(':')[1].split('-')[1]
          type = index.split('\n')[0].split(':')[0]
        }
        for (var j = start; j <= end; j++) {
          var elemId = '[id^="residue_' + j + '_"]'
          // var type = document.querySelector(elemId).id.split('_')[2]
          d3.select(elemId + '> rect').attr({
            fill: _this.getStructureColor(type)
          })
        }
      })
    },
    setStructFeatmap (selectedClus) {
      var _this = this
      // Select correct region in the coverage map
      d3.selectAll('[id^="residue_"')[0].map(function (d, i) {
        var density = d.id.split('_')[3]
        d3.select(d)
          .select('rect')
          .attr('fill', 'hsl(0, 0%,' + _this.densityScale(Math.log(density)) + '%)')
      })
      var secondaryStruct = store.getters.getSecondaryStructure[0]
      var seqStart = selectedClus.seq_start[0]
      var seqEnd = selectedClus.seq_end[selectedClus.seq_start.length - 1]
      var width = (seqEnd - seqStart) * _this.coverageMapsrw + _this.coverageMapsrw

      if (document.querySelector('#map > #coverageMap > rect')) {
        document.querySelector('#map > #coverageMap > rect').remove()
      }
      d3.select('#map > #coverageMap > g > #selection')
        .attr('width', width)
        .attr('height', 30)
        .attr('transform', 'translate(' + (seqStart * _this.coverageMapsrw - _this.coverageMapsrw) + ',' + 11 + ')')
        .attr('fill', 'white')
        .attr('stroke', '#434343')
        .attr('stroke-width', 1)
        .attr('rx', 6)
        .attr('ry', 6)

      secondaryStruct.map(function (index) {
        if (index.end) {
          var start = index.start
          var end = index.end
        } else {
          start = index.split('\n')[0].split(':')[1].split('-')[0]
          end = index.split('\n')[0].split(':')[1].split('-')[1]
        }
        for (var j = start; j <= end; j++) {
          var elemId = '[id^="residue_' + j + '_"]'
          // var type = document.querySelector(elemId).id.split('_')[2]
          d3.select(elemId + '> rect').attr({
            fill: '#cccccc'
          })
        }
      })
    },
    getStructureColor (type) {
      if (type === 'SHEET') {
        return '#f6c719'
      } else if (type === 'COIL') {
        return '#6f9e52'
      } else {
        return '#6491b6'
      }
    },
    drawDarkRegions (data, svg) {
      var arr = []
      var _this = this
      data.densityMap.forEach(function (item, index) {
        if (item === 0) {
          var n = index + 1
          svg
            .append('g')
            .attr('id', 'residue_' + n + '_Dark_0')
            .append('rect')
            .attr('transform', 'translate(' + (n * _this.coverageMapsrw - _this.coverageMapsrw) + ',' + 25 + ')')
            .attr('width', function () {
              if (_this.coverageMapsrw < 0.5) {
                return _this.coverageMapsrw + 0.25
              } else {
                return _this.coverageMapsrw
              }
            })
            .attr('height', 2)
            .attr('fill', '#434343')
            .attr('opacity', 0)
            .attr('stroke-width', 1)
            .on('mouseover', function (d) {
              _this.residueColor = d3.select(this).style('fill') // Store original residue color
              d3.select(this)
                .attr('stroke', 'white')
                .attr('stroke-width', 1)
              store.commit('setPopupTitle', 'Residue ' + this.parentElement.id.split('_')[1] + ' : Dark Region')
              store.commit('setDensityScore', data.densityMap)
              store.commit('setPopupText', 'Structures with residue ' + this.parentElement.id.split('_')[1] + ': ')
              store.commit('setTotalPDBres', this.parentElement.id.split('_')[3])
              store.commit('setPopupCoordinates', this.getBoundingClientRect())
            })
            .on('mouseout', function () {
              store.commit('setPopupCoordinates', 0)
              d3.select(this)
                .attr('stroke', _this.residueColor)
                .attr('stroke-width', 0)
            })
        }
      })
      return arr
    }
  }
}
</script>

<style scoped>
#map {
  top: var(--spacer-bottom);
  height: 3rem;
}
